import React from "react"
import { Link } from "gatsby"
import * as styles from "./postcardList.module.scss"

export default function PostcardList(props) {
  const { posts } = props
  return (
    <div className={styles.grid}>
      {posts.map(post => (
        <div className={styles.postcard}>
          <h2>
            <Link to={post.node.fields.slug} className={styles.postcardTitle}>
              {post.node.frontmatter.title}
            </Link>
          </h2>
          <div className="postcardMeta">
            <span className="date">{post.node.frontmatter.date}</span>
            <span className={styles.postMeta}>
              <svg
                aria-hidden="true"
                focusable="false"
                data-prefix="fas"
                data-icon="tag"
                class="svg-inline--fa fa-tag fa-w-16"
                role="img"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 512 512"
                width="32"
                height="32"
              >
                <path
                  fill="currentColor"
                  d="M0 252.118V48C0 21.49 21.49 0 48 0h204.118a48 48 0 0 1 33.941 14.059l211.882 211.882c18.745 18.745 18.745 49.137 0 67.882L293.823 497.941c-18.745 18.745-49.137 18.745-67.882 0L14.059 286.059A48 48 0 0 1 0 252.118zM112 64c-26.51 0-48 21.49-48 48s21.49 48 48 48 48-21.49 48-48-21.49-48-48-48z"
                ></path>
              </svg>
              {post.node.frontmatter.category}
            </span>
          </div>
        </div>
      ))}
    </div>
  )
}
